@import url(https://cdn.bootcss.com/font-awesome/4.7.0/css/font-awesome.css);
.appBar{display:flex;align-items:center;height:44px;font-size:15px;z-index:1}.appBar h1{display:flex;justify-content:center;padding-top:7px;flex:1 1}.appBar span{padding:0 20px}
.appAvatar{color:#fff;display:flex;flex-direction:column;align-items:flex-start;padding:0 40px;padding-top:37px}.appAvatar.avatar-selected{-webkit-transform:translate3d(0, 20px, 0);transform:translate3d(0, 20px, 0);opacity:0}.appAvatar .avatar-face{box-shadow:0 5px 10px rgba(0,0,0,.2)}.appAvatar .avatar-name{color:#fff;margin-top:32px;padding:0 6px;font-size:32px;letter-spacing:1px;font-weight:300}.appAvatar .avatar-tips{margin-top:16px;padding:0 6px;font-size:14px;font-weight:300;opacity:.8;line-height:1.6em}.appAvatar .avatar-date{margin-top:20px;margin-bottom:30px;padding:0 6px;font-size:14px}
.gradientColor{position:absolute;width:100%;height:100%;opacity:0;-webkit-transition:opacity .5s ease;transition:opacity .5s ease}.gradientColor-active{opacity:1}
.gradient{position:fixed;top:0;bottom:0;left:0;right:0;z-index:-1}
.task{display:flex;padding:12px 0;border-bottom:1px solid #eee;font-size:14px}.task input{display:block}.task .task-title{flex:1 1}
.todo{flex:1 1;margin:0 8px;overflow:hidden;box-shadow:0 10px 10px rgba(0,0,0,.2);color:#666}.todo-head{display:flex;padding:20px;height:27px;justify-content:space-between;align-items:flex-start;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0);will-change:transform}.todo-body{padding:0 20px;-webkit-transform:translate3d(0, 137px, 0);transform:translate3d(0, 137px, 0);will-change:transform}.todo-icon{display:flex;width:44px;height:44px;border:1px solid #ddd;border-radius:100%;justify-content:center;align-items:center;font-size:18px}.todo-menu{color:#ddd}.todo-tips{opacity:.6;font-size:13px;font-weight:600}.todo-title{margin-top:6px;font-size:32px}.todo-progress{display:flex;align-items:center;margin-top:17px}.todo-progress-line{margin-right:10px;flex:1 1;height:3px;background-color:#eee}.todo-progress-line i{display:block;height:100%;-webkit-transition:all .3s ease;transition:all .3s ease}.todo-progress-num{font-size:12px}.todo-tasks-today,.todo-tasks-done{margin-top:32px;margin-bottom:8px;color:#999}
.todoList{height:330px;-webkit-transition:all .5s ease;transition:all .5s ease}.todoList>ul,.todoList>ul>li{display:flex;height:100%}.todoList>ul>li{flex:1 1;-webkit-transition:-webkit-transform .5s ease;transition:-webkit-transform .5s ease;transition:transform .5s ease;transition:transform .5s ease, -webkit-transform .5s ease}.todoList .todo{border-radius:8px;background-color:#fff}.todoList .todoList-selected{-webkit-transform:scaleX(1.25);transform:scaleX(1.25)}
.todo-detail{position:fixed;top:0;right:0;width:100%;height:100%;display:flex;flex-direction:column;border-radius:0;background-color:#fff;color:#666;will-change:top,left,width,height}.todo-detail .todo{margin:0;margin-top:-44px;padding:0 20px;box-shadow:none}
.floating-button{position:fixed;right:44px;bottom:64px;z-index:100;width:44px;height:44px;border-radius:50%;border:none;outline:none;color:#fff;font-size:27px;box-shadow:0 5px 10px rgba(0,0,0,.2);display:flex;justify-content:center;align-items:center;-webkit-transition:all 300ms ease;transition:all 300ms ease}.floating-button.floating-button-editing{right:0;bottom:0;width:100%;border-radius:0}
.todoEditing{position:fixed;top:0;left:0;width:100%;height:100%;background:#fff;color:#666}.todoEditing .todo-editing-wrap{display:flex;flex-direction:column;align-items:center}.todoEditing .todo-editing-wrap .todo-editing-head{padding:40px 0;color:#999;font-size:17px}.todoEditing .todo-editing-wrap .todo-editing-body textarea{width:100%;border:none;outline:none;resize:none;color:#666;font-size:32px;line-height:1.2em}.todoEditing .todo-editing-wrap .todo-editing-body .todo-editing-meta{display:flex;height:44px;align-items:center;border-bottom:1px solid #eee;color:#999}.todoEditing .todo-editing-wrap .todo-editing-body .todo-editing-meta i{padding-right:10px}
.home{width:100vw;height:100vh;overflow:hidden;color:#000}
*{margin:0;padding:0;box-sizing:border-box}a{text-decoration:none}ul,ol{list-style:none}h1,h2,h3,h4{font:inherit}input,select,button{font:inherit}html,body{height:100%;-webkit-user-select:none;-webkit-touch-callout:none;-webkit-tap-highlight-color:transparent}body{font-family:-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,"Open Sans","Helvetica Neue",sans-serif}.app{height:100%;overflow:hidden}
